import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserService } from '../../shared/services/user.service';
import { Router } from '@angular/router';
import { Credentials } from '../../shared/Models/credentials';
import { finalize } from 'rxjs/internal/operators/finalize';
import { catchError } from 'rxjs/internal/operators/catchError';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-register',
  templateUrl: './register-form.component.html',
  styleUrls: ['./register-form.component.css']
})
/**
 * Komponent odpowiada za rejestracje uzytkownika w serwisie
 */
export class RegisterFormComponent implements OnDestroy {
  errors: string;
  isRequesting: boolean;
  isRegistered = false;
  submitted = false;
  credentials: Credentials = { email: '', password: '' };
  sub: Subscription;
  passNotEq: boolean;

  constructor(private userService: UserService, private router: Router) { }

  /**
   * Wyslil dane uzytkownika do rejestracji
   */
  register({ value, valid }: { value: any, valid: boolean }) {
    if (value.password !== value.matchpassword) {
      this.passNotEq = true;
      return;
    } else {
      this.passNotEq = false;
    }
    this.submitted = true;
    this.isRequesting = true;
    this.errors = '';
    if (valid) {
      this.sub = this.userService.register(value.email, value.password)
        .pipe(
          finalize(() => this.isRequesting = false),
          catchError(this.userService.handleErrors)
        )
        .subscribe(
          result => {
            if (result) {
              this.isRegistered = true;
            }
          },
          error => this.errors = error);
    }
  }

  ngOnDestroy(): void {
    if (this.sub !== undefined) {
      this.sub.unsubscribe();
    }
  }
}
