import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserService } from '../../shared/services/user.service';
import { finalize } from 'rxjs/internal/operators/finalize';
import { catchError } from 'rxjs/internal/operators/catchError';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-forgot-password-form',
  templateUrl: './forgot-password-form.component.html',
  styleUrls: ['./forgot-password-form.component.css']
})

// Komponent ten odpowiada za wysłanie adresu Url na email potrzebnego do zresetowania hasła użytkownika
export class ForgotpasswordFormComponent implements OnDestroy {
  errors: string;
  isRequesting: boolean;
  isSent = false;
  submitted = false;
  userEmail = '';
  sub: Subscription;

  constructor(private userService: UserService) { }

  /**
   * Wyslil link potwierdzjacy
   */
  sendLink({ value, valid }: { value: any, valid: boolean }) {
    this.submitted = true;
    this.isRequesting = true;
    this.errors = '';

    // jesli forma jest poprawna
    if (valid) {
      this.sub = this.userService.forgotPassword(value.email).pipe(
        finalize(() => this.isRequesting = false),
        catchError(this.userService.handleErrors))
        .subscribe(
          res => {
            this.isSent = true;
          },
          errors => {
            this.errors = errors;
          });
    }
  }

  ngOnDestroy(): void {
    if (this.sub !== undefined) {
      this.sub.unsubscribe();
    }
  }
}
