import { Subscription } from 'rxjs/internal/Subscription';
import { catchError, finalize } from 'rxjs/operators';
import { UserService } from '../../shared/services/user.service';
import { Component, OnInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-send-confirmation-link-form',
  templateUrl: './send-confirmation-link-form.component.html',
  styleUrls: ['./send-confirmation-link-form.component.css']
})

/**
 * Komponent odpowiada za widok  wysyłajacy link potwierdzajacy adres email
 */
export class SendConfirmationLinkFormComponent implements OnDestroy {
  errors: string;
  isRequesting: boolean;
  isSent = false;
  submitted = false;
  userEmail = '';
  sub: Subscription;

  constructor(private userService: UserService) { }

  // wyslil link potwierdzjacy
  sendLink({ value, valid }: { value: any, valid: boolean }) {
    this.submitted = true;
    this.isRequesting = true;
    this.errors = '';

    if (valid) {
      this.sub = this.userService.sendEmaiConfirmationLink(value.email).pipe(
        finalize(() => this.isRequesting = false),
        catchError(this.userService.handleErrors))
        .subscribe(
          res => {
            this.isSent = true;
          },
          errors => {
            this.errors = errors;
          });
    }
  }

  ngOnDestroy(): void {
    if (this.sub !== undefined) {
      this.sub.unsubscribe();
    }
  }
}
